import React, {useContext} from "react";
import {Navbar} from "react-bootstrap";
import {useCookies} from "react-cookie";
import {GlobalContext} from "./GlobalContext";

function Header() {
    const {currPage} = useContext(GlobalContext);
    const [currentPage, setCurrentPage] = currPage;
    const [theme, setTheme] = useCookies(['theme']);
    const currentTheme = theme === undefined ? 'light-mode' : theme.theme;
    return (
        <>
            <Navbar className={currentTheme} fixed='top'>
                <Navbar.Brand>
                    <img
                        alt=""
                        src="/logo.svg"
                        width="30"
                        height="30"
                        className="d-inline-block align-top"
                    />{' '}
                    {currentPage[0].toUpperCase() + currentPage.slice(1)}
                </Navbar.Brand>
            </Navbar>
        </>
    );
}

export default Header;